.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

li a {
  display: block;
  color: white;
  padding: 8px 16px;
  text-decoration: none;
  font-weight: bold;
  font-family: "Helvetica Neue", Helvetica, Arial, "Segoe UI", sans-serif;
  font-size: 25px;
}

/* Change the link color on hover */
li a:hover {
  /* background-color: rgb(32, 31, 43); */
  color: white;
  border-bottom: 1px;
  border-bottom-style: solid;
  border-color: white;
}

.logo {
  color: white;
}
.navMenu {
}
.navIcon {
  color: red;
  font-size: 30px;
  margin-top: 15px;
}
.screenContainer {
  width: 100%;
}
.leftContainer {
  /* background-color: red; */
  width: 70%;
  float: left;
}
.rightContainer {
  /* background-color: blue; */
  position: fixed;
  overflow: hidden;
  height: 100vh;
  width: 30%;
  left: 70%;
  float: left;
  background: rgb(18, 97, 158);
  background: linear-gradient(
    180deg,
    rgba(18, 97, 158, 1) 20%,
    rgba(146, 216, 212, 1) 100%
  );
  padding-left: 30px;
}
.cardContainer {
  background-color: white;
  width: 70%;
  height: 150px;
  border: #6d6d6d17 solid 3px;
  box-shadow: 4px 4px 5px 1px rgba(0, 0, 255, 0.2);
  padding-left: 30px;
  margin-bottom: 25px;
  text-align: left;
}
